@keyframes show-modal {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.modal {
    width: 100%;
    max-width: 370px;
    z-index: 30;
    background: #f9f3e5;
    border: 4px solid #33322e;
    box-shadow: 7px 7px 0px #33322e;
    padding: 5px;
    animation: show-modal 0.4s linear forwards;

    &__header {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-bottom: 12px;

        &__title {
            font-size: 19px;
            font-weight: 500;
        }

        &__close-icon {
            width: 22px;
            height: 22px;
            cursor: pointer;
        }
    }

    &__content {
        width: 100%;
        margin-bottom: 9px;

        &__title {
            font-size: 18px;
            font-weight: 500;
            margin-bottom: 16px;
            border: none;
            background: inherit;
        }

        &__text {
            width: 100%;
            resize: none;
            border: none;
            font-size: 18px;
            background: inherit;
        }
    }

    &__footer {
        width: 100%;
        display: flex;
        justify-content: center;
        margin-bottom: 5px;
    }

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        min-height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 10;
        background: rgba(0, 0, 0, 0.5);

        &.hidden {
            display: none;
        }
    }
}

.letter-counter-container {
    width: 100%;
    display: flex;
    justify-content: end;
}